@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap')


body 
  display: flex
  position: relative
  width: 100%
  height: 100vh
  margin: 0 auto
  padding: 0
  background-image: url('./../images/ROAD.jpg')
  background-repeat: no-repeat
  background-size: cover
  max-width: 1920px
#root 
  margin: 0 auto
  width: 100%
  display: flex
  align-items: center
.cvetok
  margin: 0 auto
  margin-top: 40px
  color:#F4A460

.app
  width: 80%
  margin: 0 auto
  display: flex
  position: relative
  flex-direction: column
  align-items: center
  align-self: center

  & > h2
      color: #f26900
      font-family: Caveat
      font-weight: 400
      font-size: 3rem
      width: 90%
      text-align: center
      margin: 0 auto
      margin-top: 50px
      margin-bottom: -10px
  & > button
      background-color: transparent
      font-family: Caveat
        
      font-size: 2rem
      border: 1px solid #DC143C .5
      cursor: pointer
      width: 80%
      margin: 0 auto
      margin-top: 50px
      color: #604a4f

  & > button:hover
      color: black

.container 
  position: relative
  overflow: hidden
  width: 100vw
  height: 100vh


.sidebar 
  height: 100%
  width: 35%
  position: absolute
  top: 0
  left: 0
  transition: transform 0.5s ease-in-out


.sidebar > div 
  height: 100%
  width: 100%
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  color: #fff


.sidebar h1 
  font-size: 40px
  margin-bottom: 10px
  margin-top: -30px


.main-slide 
  height: 100%
  position: absolute
  top: 0
  left: 35%
  width: 65%
  transition: transform 0.5s ease-in-out


.main-slide > div 
  background-repeat: no-repeat
  background-size: cover
  background-position: center center
  height: 100%
  width: 100%


button 
  background-color: #fff
  border: none
  color: #aaa
  cursor: pointer
  font-size: 16px
  padding: 15px


button:hover 
  color: #222


button:focus 
  outline: none


.container .controls button 
  position: absolute
  left: 35%
  top: 50%
  z-index: 100


.container .controls .down-button 
  transform: translateX(-100%)
  border-top-left-radius: 5px
  border-bottom-left-radius: 5px


.container .controls .up-button 
  transform: translateY(-100%)
  border-top-right-radius: 5px
  border-bottom-right-radius: 5px

h1
  font-family: Caveat

p
  font-family: Caveat
